@import "sass/index";

.wrapper {
  width: 100%;
  margin-top: spacing(3);
  color: use(--template-input-label-color);
}

.formBox {
  width: 100%;
  margin: 0;
  border: none;
}

.title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1rem;
  margin: 0;
  color: use(--template-input-label-color);
}

.check {
  // color: theme.palette.common.white;
}

.checked {
  color: use(--template-input-label-color) !important;
}

.inputWrapper {
  position: relative;
}

.rootInput {
  width: calc(100% - #{spacing(4)});
  margin: 0;
  height: 40px;
  margin-left: spacing(4);
  color: rgba($black, 0.87);
  border-radius: 4px;
}

.text {
  color: use(--template-input-label-color);
}

.marginTop {
  margin-top: spacing(1);
}

.radio {
  &Label {
    color: use(--template-input-label-color);
    font-size: 14px;
  }
  &Round {
    background-color: use(--template-modal-title-color);
  }
  &Radio {
    border-color: use(--template-modal-title-color);
  }
}
