@import "sass/index";

.input {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;

  height: 40px;

  border-radius: spacing(0.5);
  position: relative;
  padding: 10px 26px 10px 8px;
  transition: border-color $defaultTransition, box-shadow $defaultTransition;
  font-family: "Gilroy";

  background-color: $white;

  &:focus {
    border-radius: spacing(0.5);
    background-color: $white;
    box-shadow: rgba($dirty-blue, 0.25) 0 0 0 0.2rem;
  }
}

.formInput {
  // margin-top: spacing(2);
  display: flex;
  flex-direction: column;
  flex: 1;
}

.formInputLabel {
  color: use(--template-input-label-color);
  font-size: spacing(1.5);
  &.Mui-focused {
    color: use(--template-input-label-color);
  }
  &.Mui-focused.Mui-error {
    color: $mui-error-main;
  }
}

div.select {
  margin-top: spacing(0.5);
  background-color: $white;
  height: 40px;
  border-radius: 4px;
  padding-right: 6px;
  padding-left: 8px;
  &Label {
    & > span {
      color: black;
      font-size: 16px;
      font-weight: 500;
    }
  }
  &ExpandIcon {
    transform: scale(0.6);
    & polyline {
      stroke: black;
    }
  }
}

.menuItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: "Gilroy";

  color: $mui-secondary-light;
}

.icon {
  height: spacing(3);
  width: spacing(3);

  margin-right: spacing(1.5);
  @media (max-width: 360px) {
    margin-right: spacing(1);
  }
}

.arrowIcon {
  right: spacing(2);
}
