@import "sass/index";

.inputsWrapper {
  display: flex;
  flex-direction: column;
  width: 100%;

  & > :not(:first-child) {
    margin-top: spacing(1.5);
  }
}

.inputWrapper {
  display: flex;
  flex-grow: 1;
  min-width: 280px;
}

.selectWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex: 1;
  & > :not(:first-child) {
    margin-left: spacing(1.5);
  }

  @media (max-width: 390px) {
    margin-top: 37px !important;
  }
}

.stopOnWrapper {
  display: flex;

  & > :not(:first-child) {
    margin-left: spacing(1.5);
  }
}

.betInput {
  height: 40px;
  border-radius: spacing(0.5);
}

.betsCountInput {
  height: 40px;
  border-radius: spacing(0.5);
  & input {
    transition: all 300ms ease-out 0s;
  }
}

.betsCountInputPadding {
  & input {
    padding-right: spacing(5);
  }
}

.column {
  height: 40px;
  border-radius: spacing(0.5);
}

.infinityIcon {
  position: absolute;
  right: spacing(1);
  width: 24px;
}

.operationContainer {
  display: flex;
  & > div {
    flex-grow: 1;
  }
  & > div:first-child {
    margin-right: 20px;
  }
}
