@import "sass/index";

.inputsBlock {
  display: flex;
  align-self: flex-start;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;

  & > :not(:first-child) {
    margin-left: spacing(1.5);
    margin-top: 0;
  }

  @media (max-width: 525.95px) {
    & > :not(:first-child) {
      margin-left: 0;
      margin-top: spacing(1.5);
    }
  }
}

.inputWrapper {
  display: flex;
  flex-grow: 1;
  min-width: 280px;

  @media (max-width: 525.95px) {
    width: 100%;
  }
}

.selectWrapper {
  display: flex;
  align-items: flex-end;
  flex-direction: row;
  & > :not(:first-child) {
    margin-left: spacing(1.5);
  }
  flex: 1;

  @media (max-width: 390px) {
    margin-top: 37px !important;
  }
}

.betInput {
  height: 40px;
  border-radius: spacing(0.5);
}
