@import "sass/index";

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;

  & > :not(:first-child) {
    margin-top: 0;
    margin-left: spacing(1.5);
  }

  @media (max-width: 900px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 420px) {
    grid-template-columns: 1fr;

    & > :nth-child(1) {
      margin-top: spacing(1.5);
      margin-left: 0;

      order: 2;
      -webkit-order: 2;
    }
    & > :nth-child(2) {
      margin-left: 0;
      order: 1;
      -webkit-order: 1;
    }
  }
}

.switchWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.switch {
  margin: 10px;
}

.textWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.text {
  text-transform: uppercase;
  font-weight: bold;
  margin: 0;
  transition: all 200ms ease-out 0s;
  color: use(--template-gameCell-switch-text);
}

.selectedText {
  color: use(--template-gameCell-switch-selectedText);
}
