@import "sass/index";

.formInput {
  width: 100%;
  margin-top: spacing(2);
  font-weight: 500;

  border: 0;
  // margin: 0;
  display: inline-flex;
  padding: 0;
  position: relative;
  min-width: 0;
  flex-direction: column;
  vertical-align: top;
}

.formInputLabel {
  color: use(--template-input-label-color);
  &.Mui-focused {
    color: use(--template-input-label-color);
  }
  &.Mui-focused.Mui-error {
    color: $mui-error-main;
  }
}

.formInputField {
  margin-top: 5px;
  padding-left: 40px;

  &[type="number"] {
    -moz-appearance: textfield;
  }
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  color: rgba($black, 0.87);

  @media (max-width: 340px) {
    font-size: 0.875rem;
  }
}

.formInputAdornment {
  position: absolute;
  display: flex;
  right: 4px;

  & > :not(:first-child) {
    margin-left: 5px;
  }

  @media (max-width: 390px) {
    right: auto;
    left: 0;
    top: 50px;
    width: 100%;
    margin: 0;
    max-height: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

.formInputStartAdornment {
  position: absolute;
  left: -3px;
}

.dollarLabel {
  margin-left: 35px;
}

.adornmentButton {
  color: $white;
  background-color: #7e7c7a;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  line-height: 14px;

  padding: spacing(1);
  min-width: inherit;

  &:hover {
    background-color: #7e7c7a;
  }
  &:focus {
    background-color: #7e7c7a;
  }
  &:active {
    background-color: #7e7c7a;
  }
  &:disabled {
    background-color: rgba(#7e7c7a, 0.5);
  }

  @media (max-width: 350px) {
    padding: spacing(1) spacing(0.75);
  }
}
