@import "sass/index";

.button {
  font-family: "Gilroy";
  font-weight: bold;
  color: #2f2b2e;
  letter-spacing: 1.44px;
  cursor: pointer;

  background-color: use(--template-gameCell-gameButtons-plinko);
  outline: none;
  border: none;

  &:hover,
  &:focus {
    background-color: use(--template-gameCell-gameButtons-plinko);
  }

  &:active {
    background-color: use(--template-gameCell-gameButtons-plinko);
  }

  &:disabled {
    background-color: use(--template-gameCell-gameButtons-plinko, 0.5);
  }

  font-size: spacing(2.25);
  height: spacing(6.25);
  min-width: 200px;
  border-radius: spacing(1);
}
